#vaslab{
    font-size:6rem;
    letter-spacing: 4rem;
    color:gray; 
    font-weight:bold;
    opacity: 0.8;
    user-select: none;
    margin-bottom: 3rem;
}

#vaslab::first-letter{
    color: #5064a0;
}

@media(max-width:768px){
    #vaslab{
        font-size: 2.4rem;
        letter-spacing: 1.6rem;
    }
}

@media(min-width:769px) and (max-width:1100px){
    
    #vaslab{
        font-size: 4rem;
        letter-spacing: 3.2rem;
    }
}