#dashMenu_con{
    position: fixed;
    top: 4vh;
    right: 4vh;
    width: 15rem;
    height: 90vh;
    border-radius: 2rem;
    box-shadow: -20px 20px 40px 0 rgba(80, 100, 160, 0.1);
    background-color: #ffffff;
    z-index: 100;
}

#dashMenu_wrapper{
    display: flex;
    flex-direction: column;
    direction: ltr;
    align-items: flex-end;
    height: 100%;
    overflow-y: auto;
    padding-bottom: 3rem;
}

/* width */
::-webkit-scrollbar {
    width: 0.7rem;
    
}

/* Track */
::-webkit-scrollbar-track {
    border-radius: 0.5rem;
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 0.5rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

#dashMenu_logo{
    height: 2.5rem;
    width: 2.5rem;
    margin-top: 1.4rem;
    margin-right: 1.4rem;
    image-rendering: auto;
}

#dashMenu_sec1{
    display: flex;
    align-items: center;
    direction: rtl;
    margin-right: 1.4rem;
    margin-top: 1.5rem;
    margin-bottom: 2rem;
}

#dashMenu_profilepic{
    height: 2.4rem;
    width: 2.4rem;
    border-radius: 0.4rem;
}

#dashMenu_username{
    font-weight: 500;
    font-size: 1.1rem;
    color: #303d68;
    margin-right: 0.8rem;
}

.dashbtn_con{
    display: flex;
    direction: rtl;
    margin-right: 1.5rem;
    margin-top: 1rem;
    cursor: pointer;
    align-items: center;
    width: 11rem;
    height: 3rem;
    padding-right: 1rem;
    border-radius: 0.8rem;
}

.dashbtn_icon{
    height: 1.7rem;
}

.dashbtn_title{
    margin-right: 0.7rem;
    font-size: 0.86rem;
    font-weight: 700;
    color: #696d79;
}

@media(max-width:768px){
    #dashMenu_con{
        display: none;
    }
}