#scty_con{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 1rem;
    width: 100%;
    min-height: 100%;
    padding-right: 20rem;
    background-color: #f6f7fa;
}

#scty_title{
    color: #192034;
    font-size: 1.5rem;
    font-weight: 700;
    margin-top: 4rem;
}

#scty_subtitle{
    direction: rtl;
    color: #696d79;
    font-size: 0.9rem;
    margin-top: 1rem;
    width: 52rem;
    text-align: right;
}

#scty_card_con{
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 2rem;
    width: 52rem;
}