.register_con{
    display: flex;
    width: 100%;
    height: auto;
    padding-bottom: 3rem;
    flex-direction: column;
    align-items: center;
    background-color: #fcfcfc;
}

.register_logo{
    position: absolute;
    top:-3rem;
    right: 0;
    font-size: 1.2rem;
    font-weight: 600;
    text-align: right;
    color: #5064a0;
}

.register_card_con{
    display: flex;
    direction: rtl;
    min-height: 30rem;
    width: 84%;
    border-radius: 1rem;
    max-width: 60rem;
    margin-top: 5rem;
    box-shadow: -10px 10px 20px 0 rgba(115, 110, 136, 0.15);
    background-color: #ffffff;
}

.register_sec{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    padding-left: 6rem;
    padding-right: 3rem;
    padding-bottom: 3rem;
    border-radius: 0 1rem 1rem 0;
} 

.register_slide_sec{
    height: auto;
    width: 40%;
    border-radius: 1rem 0 0 1rem;
    background-color: rgba(131, 145, 184, 0.07);
}

.register_title_sec{
    display: flex;
    direction: rtl;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    margin-top: 3rem;
    margin-bottom: 2rem;
}

.register_title{
    color: #192034;
    font-size: 1.2rem;
    font-weight: 700;
}

.register_go_login{
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    color: #5064a0;
    cursor: pointer;
    font-weight: 500;
}

.register_go_login_arrow{
    image-rendering: unset;
    height: 1rem;
    margin-right: 0.7rem;
}

.register_rules_sec{
    width: 80%;
    margin-top: 1rem;
    font-size: 0.9rem;
    display: flex;
    direction: rtl;
    align-items: center;
    font-weight: 500;
}

.register_rules{
    color: #5064a0;
    cursor: pointer;
    margin-left: 0.2rem;
}

.register_rules_checkbox{
    top:0.2rem;
    margin-left: 0.5rem !important;
}

.register_btn{
    margin-top: 1rem;
    cursor: pointer;
    height: 3rem;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 0.5rem;
    box-shadow: 0 8px 30px 0 rgba(80, 100, 160, 0.5);
    background-color: #5064a0;
}

.register_btn_icon{
    image-rendering: unset;
    height: 0.9rem;
    margin-right: 0.5rem;
}

.register_newnumber_con{
    display: flex;
    direction: rtl;
    align-items: center;
    margin-top: 1rem;
    font-weight: 500;
    font-size: 0.9rem;
}

.register_newnumber_link{
    color: #5064a0;
    cursor: pointer;
}

.register_newnumber_arrow{
    height: 0.8rem;
    cursor: pointer;
    image-rendering: unset;
    margin-right: 0.5rem;
}

