.login_con{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card_con{
    display: flex;
    flex-direction: column;
    direction: rtl;
    width: 26rem;
    height: 32rem;
    align-items: center;
    border-radius: 15px;
    box-shadow: -10px 10px 20px 0 rgba(115, 110, 136, 0.15);
    background-color: #ffffff;
}

.login_logo{
    position: absolute;
    width: 3rem;
    height: 3rem;
    top: 2rem;
    right: 2.3rem;
}

.login_card_title{
    width: 80%;
    font-size: 1.4rem;
    font-weight: 700;
    margin-top: 7rem;
    text-align: right;
}

.login_btn{
    display: flex;
    width: 80%;
    height: 3rem;
    margin-top: 2rem;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    color: white;
    border-radius: 8px;
    user-select: none;
    box-shadow: 0 8px 30px 0 rgba(80, 100, 160, 0.5);
    background-color: #5064a0;
}

.login_btn:active{
    opacity: 0.9;
}

.login_go_signup{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.85rem;
    font-weight: 500;
    color: #192034;
    margin-top: 1rem;
}

.login_go_signup span{
    color: #5064a0;
    cursor: pointer;
}

.login_left_arrow{
    height: 0.9rem;
    width: 0.9rem;
    margin-right: 0.7rem;
}