#btf_con{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 1rem;
    width: 100%;
    min-height: 100%;
    padding-right: 20rem;
    background-color: #f6f7fa;
}

#btf_card_con{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    direction: rtl;
    position: relative;
    margin-top: 2rem;
    width: 52rem;
    height: 30rem;
    border-radius: 1rem;
    background-color: white;
}

#btf_success_img{
    height: 5rem;
    width: 5rem;
}

#btf_title{
    font-size: 1.3rem;
    color: #192034;
    font-weight: 700;
    margin-top: 1rem;
}

#btf_text1{
    color: #696d79;
    font-size: 1rem;
    margin-top: 2rem;
}

#btf_error{
    border-radius: 0.5rem;
    font-size: 1.1rem;
    font-weight: 700;
    color: #c40a6b;
    padding: 1rem 1.6rem;
    margin-top: 0.5rem;
}

#btf_btn{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    width: 12rem;
    height: 3rem;
    margin-top: 1.4rem;
    border-radius: 0.5rem;
    background-color: #5064a0;
    cursor: pointer;
}

@media(max-width:768px){
    #btf_con{
        padding-right: 0;
        padding-top: 2rem;
        padding-bottom: 3rem;
        align-items: center;
    }

    #btf_card_con{
        width: 86%;
        height: auto;
        padding: 4rem 1rem;
    }

    #btf_title{
        font-size: 1.2rem;
    }

    #btf_text1{
        font-size: 0.92rem;
    }
    
    #btf_error{
        font-size: 1rem;
    }
    
    #btf_btn{
        font-size: 0.92rem;
        width: 10rem;
        height: 2.8rem;
    }
}

@media(min-width:769px) and (max-width:1100px){
    
    #btf_card_con{
        width: 28rem;
        height: auto;
        padding: 4rem 1rem;
    }
}