#bdt_con{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 1rem;
    width: 100%;
    min-height: 100%;
    padding-right: 20rem;
    background-color: #f6f7fa;
}

#bdt_title{
    color: #192034;
    font-size: 1.5rem;
    font-weight: 700;
    margin-top: 4rem;
}

#bdt_subtitle{
    direction: rtl;
    color: #696d79;
    font-size: 0.9rem;
    margin-top: 1rem;
    width: 52rem;
    text-align: right;
}

#bdt_card_con{
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 2rem;
    width: 52rem;
    height: 34rem;
    border-radius: 1rem;
    background-color: white;
}

#bdt_card_sec1{
    display: flex;
    height: 90%;
    width: 100%;
}

#bdt_card_sec3{
    display: flex;
    justify-content: flex-end;
    width: 38%;
    height: 100%;
}

#bdt_alertcon{
    background-color: #eaefff;
    color: #5064a0;
    font-size: 1rem;
    height: 13rem;
    width: 17.8rem;
    padding: 1rem 1.5rem;
    margin-top: 2rem;
    direction: rtl;
    text-align: right;
    border-radius: 1rem;
    line-height: 2rem;
    font-weight: 700;
}
#bdt_alertcon p {
    font-weight: 500;
    font-size: 0.9rem;
}

#bdt_card_sec4{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 62%;
    height: 100%;
}

#bdt_card_fa_price{
    font-size: 0.88rem;
    color: gray;
    padding-right: 0.2rem;
    width: 80%;
    text-align: right;
    direction: rtl;
    margin-top: 0.3rem;
    margin-bottom: -0.5rem;
}

#bdt_card_desc{
    width: 80%;
    resize: none;
    height: 5.2rem;
    border-radius: 0.5rem;
    margin-top: 1.5rem;
    direction: rtl;
    padding: 0.2rem 0.6rem;
    font-size: 0.84rem;
    border: 1px #dadada solid;
}

#bdt_card_sec2{
    display: flex;
    flex-direction: row;
    direction: ltr;
    height: 10%;
    width: 100%;
    padding-left: 2rem;
}

#bdt_btn_accept{
    background-color: #5064a0;
    color: white;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.2rem;
    margin: 0 1rem;
    width: 12rem;
    border-radius: 0.5rem;
    cursor: pointer;
    padding-top: 0.2rem;
}

#bdt_btn_cancel{
    border: solid 1px rgba(196, 10, 107, 0.3);
    color: #c40a6b;
    padding-top: 0.2rem;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.2rem;
    margin: 0 1rem;
    width: 8rem;
    border-radius: 0.5rem;
    cursor: pointer;
}

#bdt_loading_blur{
    position: fixed;
    height: 100vh;
    width: 100%;
    right: 0;
    top: 0;
    backdrop-filter: blur(5px);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

#bdt_loading_blur_warrper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

#bdt_sync_icon{
    width: 4rem;
    animation-name: anim1;
    animation-duration: 1s;
    user-select: none;
    transform: rotateZ(0deg);
    animation-iteration-count: infinite;
}

@keyframes anim1{
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform:rotateZ(-360deg);
    }
}

#bdt_sync_text{
    margin-top: 2rem;
    font-size: 1.1rem;
    color: #5064a0;
    user-select: none;
}

@media(max-width:768px){
    #bdt_con{
        padding-right: 0;
        align-items: center;
    }

    #bdt_subtitle{
        width: 86%;
    }

    #bdt_card_con{
        width: 86%;
        height: auto;
    }

    #bdt_card_sec1{
        flex-direction: column;
        align-items: center;
    }

    #bdt_card_sec3{
        width: 90%;
        align-items: center;
        justify-content: center;

    }

    #bdt_card_sec4{
        width: 90%;
    }

    #bdt_card_sec2{
        margin-top: 2rem;
        padding-bottom: 2rem;
        justify-content: center;
    }
}

@media(min-width:769px) and (max-width:1100px){
    
    #bdt_subtitle{
        width: 28rem;
    }

    #bdt_card_con{
        width: 28rem;
        height: auto;
    }

    #bdt_card_sec1{
        flex-direction: column;
        align-items: center;
    }

    #bdt_card_sec3{
        width: 90%;
        align-items: center;
        justify-content: center;

    }

    #bdt_card_sec4{
        width: 90%;
    }

    #bdt_card_sec2{
        margin-top: 2rem;
        padding-bottom: 2rem;
    }
}