/* @import '~antd/dist/antd.less'; */

*{
  position: relative;
  font-family: dana;
  box-sizing: border-box;
  padding: 0rem;
  margin: 0rem;
  scroll-behavior: smooth;
}

img {
  image-rendering: auto !important;
}

html{
	height: 100%;
  	width: 100%;
}

body{
	height: 100%;
  	width: 100%;
}

#root{
	height: 100%;
  	width: 100%;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  background-color: #fcfcfc;
}

input{
	border: unset;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.ant-notification-notice-message{
	direction: rtl !important;
}


@font-face {
	font-family: dana;
	font-style: normal;
	font-weight: 300;
	src: url(assets/fonts/dana-fanum-light.woff) format("woff");
}

@font-face {
	font-family: dana;
	font-style: normal;
	font-weight: 400;
	src: url(assets/fonts/dana-fanum-regular.woff) format("woff");
}

@font-face {
	font-family: dana;
	font-style: normal;
	font-weight: 500;
	src: url(assets/fonts/dana-fanum-medium.woff) format("woff");
}

@font-face {
	font-family: dana;
	font-style: normal;
	font-weight: 700;
	src: url(assets/fonts/dana-fanum-bold.woff) format("woff");
}

@font-face {
	font-family: dana;
	font-style: normal;
	font-weight: 900;
	src: url(assets/fonts/dana-fanum-extrabold.woff) format("woff");
}